<template>
  <div class="scroll-downs" v-scroll-to="'#' + targetElement">
    <div class="mousey">
      <div class="scroller"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ScrollDown',
  props: ['targetElement'],
  data () {
    return {
    };
  },
  mounted () {
    console.log(this.targetElement);
  }
};
</script>

<style scoped lang="scss">
  .scroll-downs {
    position: absolute;
    /*top: 0;*/
    right: 0;
    bottom: 15px;
    left: 0;
    margin: auto;

    width :34px;
    height: 55px;
    cursor: pointer;
    z-index: 9;
  }
  .mousey {
    width: 3px;
    padding: 10px 15px;
    height: 35px;
    border: 2px solid #fff;
    border-radius: 25px;
    opacity: 0.75;
    box-sizing: content-box;
  }
  .scroller {
    width: 3px;
    height: 10px;
    border-radius: 25%;
    background-color: #fff;
    animation-name: scroll;
    animation-duration: 2.2s;
    animation-timing-function: cubic-bezier(.15,.41,.69,.94);
    animation-iteration-count: infinite;
  }
  @keyframes scroll {
    0% { opacity: 0; }
    10% { transform: translateY(0); opacity: 1; }
    100% { transform: translateY(15px); opacity: 0;}
  }

</style>
