import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';

import VuePageTransition from 'vue-page-transition';
import vueSmoothScroll from 'vue2-smooth-scroll';

import './registerServiceWorker';

import 'owl.carousel/dist/assets/owl.carousel.min.css';
// import 'owl-carousel/dist/assets/owl.theme.default.min.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'jquery/src/jquery.js';
import 'popper.js/dist/popper';
import 'bootstrap/dist/js/bootstrap.min.js';

import Typed from 'typed.js/lib/typed.min.js';
import 'drawsvg/public/jquery.drawsvg.min.js';
import Vivus from 'vivus/dist/vivus.min.js';
import 'owl.carousel/dist/owl.carousel.min.js';
import 'ejs/ejs';
import VueScrollTo from 'vue-scrollto';

import { library, dom } from '@fortawesome/fontawesome-svg-core';
import { fab } from '@fortawesome/free-brands-svg-icons';
import { fas } from '@fortawesome/free-solid-svg-icons';
import { far } from '@fortawesome/free-regular-svg-icons';

import Header from './components/Header';

import './assets/css/style.css';
import VueResource from 'vue-resource';
import { SliderPlugin } from '@syncfusion/ej2-vue-inputs';
import { VLazyImagePlugin } from 'v-lazy-image';
import VueLazyLoadVideo from 'vue-lazyload-video';
// import { SliderTooltipEventArgs, SliderTickEventArgs } from "@syncfusion/ej2-inputs";
// import { isNullOrUndefined } from "@syncfusion/ej2-base";

library.add(fab, fas, far);
dom.watch();

global.Typed = Typed;
global.Vivus = Vivus;

Vue.config.productionTip = false;
Vue.component('app-header', Header);
Vue.use(VuePageTransition);
Vue.use(vueSmoothScroll);
Vue.use(VueResource);
Vue.use(SliderPlugin);
Vue.use(VueScrollTo);
Vue.use(VLazyImagePlugin);
Vue.use(VueLazyLoadVideo);

// Vue.http.options.root = 'https://gdofficial-stage.gendigihost.com';
// Vue.http.options.root = 'http://dummy.restapiexample.com';

router.beforeEach((to, from, next) => {
  const nearestWithTitle = to.matched.slice().reverse().find(r => r.meta && r.meta.title);

  const nearestWithMeta = to.matched.slice().reverse().find(r => r.meta && r.meta.metaTags);
  // const previousNearestWithMeta = from.matched.slice().reverse().find(r => r.meta && r.meta.metaTags);

  if (nearestWithTitle) document.title = nearestWithTitle.meta.title;

  Array.from(document.querySelectorAll('[data-vue-router-controlled]')).map(el => el.parentNode.removeChild(el));

  if (!nearestWithMeta) return next();

  nearestWithMeta.meta.metaTags.map(tagDef => {
    const tag = document.createElement('meta');

    Object.keys(tagDef).forEach(key => {
      tag.setAttribute(key, tagDef[key]);
    });

    tag.setAttribute('data-vue-router-controlled', '');

    return tag;
  })
    .forEach(tag => document.head.appendChild(tag));

  next();
});

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app');
