<template>
  <div class="section contact">
    <div class="container">
      <div class="row">
        <div class="col-md-12">
           <h2 class="heading text-center">
            Contact Us
          </h2>
          <h2 class="text-center mb-5">Drop Us A Line, wed love to hear from you. Call <a href="tel:+91 9176333857">+91 9176333857</a> to talk to one of our digital experts today or visit our Hyderabad Office for chat and coffee. We're always keen to discuss new ideas and technologies.</h2>
        </div>
      </div>
      <div class="row">
        <div class="col-md-8 offset-md-2">
          <form @submit.prevent="submitForm" name="contactForm">
            <div class="form-row">
              <div class="form-group col-md-6">
                <input v-model="first_name" type="text" class="form-control form-control-lg" id="first-name" required>
                <label class="form-control-placeholder" for="first-name">Name</label>
              </div>
              <div class="form-group col-md-6">
                <input v-model="subject"  type="text" class="form-control form-control-lg" id="last-name" required>
                <label class="form-control-placeholder" for="last-name">Subject</label>
              </div>
            </div>
            <div class="form-row">
              <div class="form-group col-md-6">
                <input v-model="from" type="text" class="form-control form-control-lg" id="email"  required>
                <label class="form-control-placeholder" for="email">Email</label>
              </div>
              <div class="form-group col-md-6">
                <input type="text" v-model="phone" class="form-control form-control-lg" id="phone" required>
                <label  class="form-control-placeholder" for="phone">Phone</label>
              </div>
            </div>
            <div class="form-row mt-4">
              <div class="form-group col-md-12">
                <textarea v-model="body" id="message" class="form-control" cols="30" rows="10" required></textarea>
                <label class="form-control-placeholder" for="message">Your Message</label>
              </div>
            </div>

            <div class="form-row mt-4">
              <div class="form-group col-md-12">
                <button class="btn btn-outline-dark" :disabled="ajaxPending" type="submit">
                  Send Message
                </button>
               <div class="d-inline-flex align-items-center" v-if="ajaxPending"> &nbsp;
                 &nbsp; <i class="fas fa-circle-notch fa-spin mt-2" style="font-size:24px"></i>
               </div>
              </div>
               <div class="col-12" ref="successAlert">
                 <div class="alert alert-success">
                   Thank you for your enquiry, we'll get back to you shortly.
                 </div>
               </div>
              <div class="col-12" ref="errorsAlert">
                <div class="alert alert-danger">
                  Sorry, some error occurred. Please try again later, or call us at +91 9176333857.
                </div>
              </div>
            </div>

          </form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Home-Contact',
  created () {
    // this.resource = this.$resource('api/v1/create');
    this.resource = this.$resource('php/mailer.php');
  },
  data: () => {
    return {
      first_name: '',
      subject: '',
      body: '',
      phone: '',
      from: '',
      resource: {},
      ajaxPending: false
    };
  },
  mounted () {
    this.$refs.successAlert.style.display = 'none';
    this.$refs.errorsAlert.style.display = 'none';
  },
  methods: {
    resetForm () {
      this.first_name = '';
      this.subject = '';
      this.body = '';
      this.phone = '';
      this.from = '';
    },
    submitForm () {
      let payload = {
        from: this.from,
        subject: this.subject,
        body: this.body,
        first_name: this.first_name,
        phone: this.phone
      };
      console.log(payload);
      this.ajaxPending = true;
      this.resource.save(payload).then(data => {
        console.log(data);
        this.ajaxPending = false;
        this.$refs.successAlert.style.display = 'block';
        setTimeout(() => {
          this.$refs.successAlert.style.display = 'none';
        }, 3000);
        this.resetForm();
      }, (err) => {
        if (err) {
          console.log(err);
        }
        this.$refs.errorsAlert.style.display = 'block';
        setTimeout(() => {
          this.$refs.errorsAlert.style.display = 'none';
        }, 3000);
        this.ajaxPending = false;
        this.resetForm();
      });
      // this.$http.post('https://gdofficial-stage.gendigihost.com/php/mailer.php', payload).then((data) => {
      //   console.log(data);
      // });
    }
  }
};
</script>

<style scoped>
  @import "../assets/css/home-contact.css";
  @media (max-width: 480px) {
    .contact-page-wrapper>.section.contact .heading {
      font-size: 4rem;
    }
  }
</style>
