<script>
// @ is an alias to /src
import HomePortfolio from '../components/HomePortfolio';
import OurClients from '../components/OurClients';
import HomeContact from '../components/HomeContact';
import Footer from '../components/Footer';
import Habitat from '../components/portfolio-items/Habitat';
import ScrollDown from '../components/ScrollDown';

export default {
  name: 'home',
  data () {
    return {
      arrayOfBrandNames: [
        "App like <span class='instagram-text'>Instagram</span>?",
        "App like <span class='whatsapp-text'>Whatsapp</span>?",
        "App like <span class='gmail-text'>Gmail</span>?",
        "App like <span class='uber-text'>Uber</span>?",
        "App like <span class='twitter-text'>Twitter</span>?",
        "App like <span class='skype-text'>Skype</span>?",
        "App like <span class='quora-text'>Quora</span>?"
      ],
      colors: {
        whatsapp: '#34af23',
        instagram: '#bc2964',
        uber: '#343439',
        skype: '#00aae8',
        quora: '#a1240f',
        gmail: '#d74937',
        twitter: '#5aa4d6'
      }
    };
  },
  components: {
    'app-home-portfolio': HomePortfolio,
    'app-habitat': Habitat,
    'app-our-clients': OurClients,
    'app-home-contact': HomeContact,
    'app-footer': Footer,
    'app-scroll-down': ScrollDown
  },
  mounted () {
    this.initDrawSvg();
    this.initBrandTypingWithAnimation('#brands-section__brand-name', this.arrayOfBrandNames);
  },
  methods: {
    initDrawSvg () {
      $.fn.drawFontSvg = function (options) {
        var settings = $.extend({
          stroke: 'currentColor',
          fill: 'transparent',
          strokeWidth: 1,
          callback: (obj) => {
          }
        }, options);

        if (this.length > 1) {
          console.error('Can only be used for 1 element at single time');
          return false;
        }
        settings = $.extend(settings, $(this).data());
        var id = 'font-animate-' + settings.brand;
        $(this).attr('id', id).find('path').each(function () {
          $(this)
            .attr('fill', settings.fill)
            .attr('stroke', settings.stroke)
            .attr('stroke-width', settings.strokeWidth);
        });
        try {
          return new Vivus(id, settings, settings.callback);
        } catch (err) {
          // console.log(err);
        }
      };
    },
    initBrandSvgAnimation (brandName, color) {
      var $el = $('.' + brandName);
      $el.drawFontSvg({
        strokeWidth: 8,
        brand: brandName,
        callback: function (arg) {
          $el.children().css('color', color);
          $el.children().css('fill', color);
        }
      });
    },
    initBrandTypingWithAnimation (selector, arrayOfBrandNames) {
      $(this.$refs.brandIcons.querySelectorAll('.brand-icon')).hide();
      if (!$(selector).length) {
        return false;
      }
      var typed = new Typed(selector, {
        strings: arrayOfBrandNames,
        smartBackspace: false, // must remain false, else icons wont animate
        typeSpeed: 60,
        backSpeed: 20,
        contentType: 'html',
        backDelay: 1500,
        loop: true,
        preStringTyped: (i, self) => {
          $('.brand-icon').hide().children().css('fill', 'transparent');
          var brandName = $.trim(self.strings[i].split("'")[1].split('-')[0]);
          $('.brand-icon.' + brandName).show();
          this.initBrandSvgAnimation(brandName, this.colors[brandName]);
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
