<template>
 <div>
   <nav ref="navbar" id="navbar" class="navbar navbar-expand-lg navbar-light gendidi-navbar fixed-top">
     <router-link tag="a" class="navbar-brand gendidi-navbar__logo" to="/">
       <img src="../assets/img/VL-Logo-2.png" alt="">
     </router-link>
     <button
       id="navbar-toggler"
       @click="toggleNav"
       class="navbar-toggler" type="button"
             aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span  v-show="!enabled">
        <i class="fas fa-bars"></i>
      </span>
      <span  v-show= "enabled" >
         <i class="fas fa-times"></i>
      </span>
     </button>

     <div class="collapse navbar-collapse" id="navbarSupportedContent">
       <ul class="navbar-nav ml-auto">
         <router-link class="nav-item  gendidi-navbar__item pr-4" tag="li" to="/">
           <a class="nav-link" href="/">Home</a>
         </router-link>
         <router-link class="nav-item  gendidi-navbar__item pr-4" tag="li" to="/about">
           <a class="nav-link">About</a>
         </router-link>
         <router-link class="nav-item  gendidi-navbar__item pr-4" tag="li" to="/services">
           <a class="nav-link">Services</a>
         </router-link>
         <router-link class="nav-item  gendidi-navbar__item pr-4" tag="li" to="/career">
           <a class="nav-link">Careers</a>
         </router-link>
         <router-link class="nav-item  gendidi-navbar__item pr-4" tag="li" to="/portfolio">
           <a class="nav-link">Portfolio</a>
         </router-link>
         <router-link class="nav-item  gendidi-navbar__item pr-4" tag="li" to="/contact">
           <a class="nav-link">Contact</a>
         </router-link>
         <router-link class="nav-item  gendidi-navbar__item pr-4" tag="li" to="/get-a-quote">
           <a class="nav-link">Get A Quote</a>
         </router-link>
       </ul>
     </div>
   </nav>
   <div id="mySidenav" class="sidenav" ref="sideNav" :class="{opened: enabled, closed: !enabled}">
     <a  class="closebtn"  @click="toggleNav"> <i class="fas fa-times"></i></a>
     <router-link to="/">HOME</router-link>
     <router-link to="/about">ABOUT</router-link>
     <router-link to="/services">SERVICES</router-link>
     <router-link to="/custom-websites">CUSTOM WEB APPLICATIONS</router-link>
     <router-link to="/mobile-apps">MOBILE APPS</router-link>
     <router-link to="/portfolio">PORTFOLIO</router-link>
     <router-link to="/contact">CONTACT</router-link>
   </div>
 </div>

</template>

<script>

export default {
  name: 'Header',
  components: {
  },
  data: function () {
    return {
      enabled: false,
      prevScrollPos: '',
      navbar: ''
    };
  },
  watch: {
    $route (to, from) {
      this.enabled = false;
    }
  },
  mounted () {
    this.navbar = this.$refs.navbar;
    this.prevScrollPos = window.pageYOffset;
    window.addEventListener('scroll', this.updateScroll);
  },
  destroy () {
    window.removeEventListener('scroll', this.updateScroll);
  },
  methods: {
    toggleNav () {
      this.enabled = !this.enabled;
    },
    updateScroll () {
      var currentScrollPos = window.pageYOffset;
      if (this.prevScrollPos > currentScrollPos) {
        this.navbar.style.top = '0';
      } else {
        this.navbar.style.top = '-100px';
      }
      this.prevScrollPos = currentScrollPos;
    }
  }
};
</script>

<style lang="scss">
  #navbar {
    transition: top 0.6s ease-out;
  }
  #navbar-toggler{
    color: #fff;
    font-size:2rem;
    border-color:#fff;
    padding:0.8rem 1.6rem;
    width:5.5rem;
  }
  .sidenav.opened{
    width: 300px;
  }
  .sidenav.closed{
    width: 0;
  }
  .sidenav {
    height: 100%; /* 100% Full-height */
    width: 0; /* 0 width - change this with JavaScript */
    position: fixed; /* Stay in place */
    z-index: 1040; /* Stay on top */
    top: 0;
    left: 0;
    background-color: #111; /* Black*/
    overflow-x: hidden; /* Disable horizontal scroll */
    padding-top: 60px; /* Place content 60px from the top */
    transition: 0.5s; /* 0.5 second transition effect to slide in the sidenav */
  }

  /* The navigation menu links */
  .sidenav a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 1.4rem;
    font-weight: 500;
    color: #818181;
    display: block;
    cursor: pointer;
    transition: 0.3s;
    font-family: 'Montserrat', sans-serif;
  }

  /* When you mouse over the navigation links, change their color */
  .sidenav a:hover {
    color: #f1f1f1;
    cursor: pointer;
  }

  /* Position and style the close button (top right corner) */
  .sidenav a.closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 2rem;
    margin-left: 50px;
    color: #818181;
  }

</style>
